.technologies {
    padding: 3rem;
      display: flex;
      width: 100%;
      box-sizing: border-box; 
      background-color: #f4f4f9;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* background-color: #222831;
  color: white */
  }
  .content {
    flex: 0 0 20%; 
    display: flex;
    align-items: center;  }
  
.inner {
    flex: 0 0 80%; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;    
    width:100%;

  }
  img.photo-technologies {
    width: 25rem;
      height: 38rem;
      
  }

  #technologies ul.badges a img {
    height: 100px;
    width: 100px;
    margin: 5px;
    /* filter: invert(100%) brightness(150%); */
  }

  #technologies ul.badges {
    list-style: none; 
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap; 
    gap: 10px;
  }
  
/* Adjustments for mobile screens */
@media (max-width: 600px) {
  .technologies {
    display: flex;
    flex-direction: column; 
    height: 100%; 
    width: 100%; 
    padding: 0;
    overflow: none;
  }
  
  .inner {
    width: 100%; /* Take full width of the parent */
    height: 100%; /* Take full height of the parent */
    display: flex;
    align-items: center; /* Vertically center content */
    justify-content: center; /* Horizontally center content */
    flex-grow: 1; /* Allow .inner to grow and fill available space */
    padding: 0;
    margin-top: 1rem;
    margin-bottom: 2rem;
  }

  /* .badges {
    display: flex;
    align-items: center;
    justify-content: center;
  } */

  .content {
    display: none;
  }

  #technologies ul.badges a img {
    height: 60px; 
    width: 60px; 
  }
}


  